.contact-details{
    .back-btn {
        margin-bottom: 30px;

    }

    img {
        border-radius: 50%;
        border: 1px solid rgb(240, 240, 240);
        height: 120px;
        width: 120px;
    }
}

.details-info {
    display: flex;
    justify-content: center;
   
}

.contact-card {
    margin: 0 auto;
    width: 400px;
    height: 200px;
    padding: 15px;
    box-shadow: 0 0 8px #0000002b;
}

.contact-actions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5px;
}

.info-txt {
    margin: 10px 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 18px;
    padding-left: 15px;


    :first-child {
        font-weight: 600;
        font-size: 20px;
    }

    
}

.edit-user-icon::before {
    font-family: fa;
    content: '\f4ff';
}
