.contact-edit {
    h1 {
        margin: 0 auto;
        width: 350px;
    }
    form {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        width: 350px;
        margin: 0 auto;
    }
    input {
        height: 35px;
        margin-bottom: 10px;
        padding-left: 10px;
        border-radius: 3px;
        border: 1px solid $clr3;

    }
}

.save-btn{
    width: 100px;
    height: 30px;
    // margin: 0 auto;
    color: $clr5;
    background-color: $clr3;
    border: none;
    border-radius: 4px;
    font-size: 16px;
}

