.contact-preview {
    display: flex;
    // flex-direction: column;
    box-shadow: 0 0 8px #0000002b;
    overflow: hidden;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    border-radius: 5px;
    padding: 10px 20px;

    img {
        border-radius: 50%;
        border: 1px solid #e4e4e4c9;
        width: 55px;
        height: 55px;
        object-fit: cover;
    }

    span {
        padding: 0 10px;
        text-align: center;
        font-size: 18px;
    }
}





.robot-preview {
    background: #03a9f414;
    box-shadow: 0 0 11px #0000002b;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    cursor: pointer;
    .info {
        padding: 10px;
        flex-grow: 1;
    }

    .actions {
        flex-basis: 25%;
        padding: 10px;
        background-color: #000000c9;
        transform: translateY(100%);
        transition: transform .2s;
    }
    
    &:hover .actions {
        transform: translateY(0);
    }

}